import React from 'react';
import Jumbo from '../components/Sections/Jumbo';
import Brief from '../components/Sections/Brief';
import maya from "../App.json";
import LoginButton from '../components/Buttons/LoginButton';


class Home extends React.Component {
  render() {
        return (
            <>
            <Jumbo title="Do More With AI" body="Maya is a new approach built for the demands, challenges and opportunities of AI today.By learning what you like and working with your favorite apps, Maya makes it easy for you to get more done" image={maya.jumbos[0]} button={<LoginButton />} />
                    {
            maya.about.slice(0,4).map(function(service,index){
            return (<Brief key={index} align={(index % 2) ? "text-right":"text-left"}  button={(index % 2) ? <LoginButton />:null}  full={false} dark={(index % 2) ? false:true} data={service} set={(index % 2) ? "first":"last"} image={service.image}/>)
            })
            }
            <Brief full={true} dark={true} align="text-center" data={maya.about[4]} set="first" image={maya.jumbos[1]}/>
            </>
        );
    }

}

export default Home;
